.particles-js canvas {
    display: block;
    vertical-align: bottom;
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
    transition: opacity .8s ease, -webkit-transform 1.4s ease;
    transition: opacity .8s ease, transform 1.4s ease;
    transition: opacity .8s ease, transform 1.4s ease, -webkit-transform 1.4s ease
  }
  
.particles-js {
    width: 100%;
    height:100vh;
    position: fixed;
    z-index: -10;
    top: 0;
    left: 0;
    background: linear-gradient(to right, rgb(32, 32, 83), rgb(88, 66, 109));

  }

.home{
    color: white;
  }

.info{
    position: relative;
    color: white;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}

.about::before{
    background: url(/static/media/aboutBackground.4c9f2260.PNG);
    background-size: cover !important;
    background-position: center !important;
    background-repeat: no-repeat !important;
    
    content: "";
    position: absolute;
    top: 0; left: 0;
    width: 100%; height: 100%;
    filter: blur(3px);
    -webkit-filter: blur(3px);
  }

.myPic{
  height: 300px;
  width: auto;
  border-radius: 5%;
}

@media screen and (max-width:800px){
